@import '../../../assets/styles/colors.scss';

.formContainer {
  background: #ffffff;
  box-shadow: 0px 1px 0px rgba(90, 97, 105, 0.11),
    0px 2px 4px rgba(90, 97, 105, 0.12), 0px 5px 5px rgba(90, 97, 105, 0.06),
    0px 3.5px 35px rgba(90, 97, 105, 0.1);
  border-radius: 9px;
  padding: 18px;

  .title {
    text-align: center;
    font-size: 17.5px;
  }

  .form {
    margin-top: 32px;

    .label {
      font-size: 10.5px;
    }
    .input {
      margin-top: 8px;
      background: #ffffff;
      border: 0.5px solid #becad6;
      box-sizing: border-box;
      border-radius: 3px;
      height: 29px;
      width: 100%;
      padding: 8px 12px;
      font-size: 11px;
      outline: none;
    }

    .controls {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 16px;
      .register {
        color: $main-blue;
        font-size: 10.5px;
      }

      .submit {
        height: 30px;
        width: 110px;
        background-color: $main-blue;
        color: white;
        border: 0.5px solid $main-blue;
        box-sizing: border-box;
        border-radius: 4px;
        font-size: 11.5px;
      }
    }
  }
}
